"use client"

import * as React from "react"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

// Generate time slots from 7 AM to 6 PM in 30-minute intervals
const generateTimeSlots = () => {
  const slots = []
  for (let hour = 7; hour <= 15; hour++) {
    for (let minute of ['00', '30']) {
      // Skip 3:30 PM as it's the last time slot
      if (hour === 15 && minute === '30') continue
      
      // Convert to 12-hour format
      let displayHour = hour % 12
      if (displayHour === 0) displayHour = 12
      const ampm = hour < 12 ? 'AM' : 'PM'
      
      const time = `${hour.toString().padStart(2, '0')}:${minute}`
      const displayTime = `${displayHour}:${minute} ${ampm}`
      
      slots.push({
        value: time,
        label: displayTime
      })
    }
  }
  return slots
}

const timeSlots = generateTimeSlots()

interface TimeSelectProps {
  value: string
  onChange: (value: string) => void
  includeLeaveTimeOption?: boolean
}

export function TimeSelect({ value, onChange, includeLeaveTimeOption = false }: TimeSelectProps) {
  return (
    <Select value={value} onValueChange={onChange}>
      <SelectTrigger className="w-full">
        <SelectValue placeholder="Straight Time Range (7:00am - 3:30pm)" />
      </SelectTrigger>
      <SelectContent>
        {includeLeaveTimeOption && (
          <SelectItem 
            value="leave"
            className="cursor-pointer hover:bg-gray-100 font-medium text-blue-600"
          >
            Leave Time
          </SelectItem>
        )}
        {timeSlots.map((slot) => (
          <SelectItem 
            key={slot.value} 
            value={slot.value}
            className="cursor-pointer hover:bg-gray-100"
          >
            {slot.label}
          </SelectItem>
        ))}
      </SelectContent>
    </Select>
  )
}
