"use client"

import * as React from "react"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card } from "@/components/ui/card"

interface JobSpecsCalculatorProps {
  onChange: (specs: {
    distanceFromObstruction: number;
    heightOfObstruction: number;
    distanceIn: number;
    totalDistance: number;
  }) => void;
}

export function JobSpecsCalculator({ onChange }: JobSpecsCalculatorProps) {
  const [distanceFrom, setDistanceFrom] = React.useState<string>('')
  const [height, setHeight] = React.useState<string>('')
  const [distanceIn, setDistanceIn] = React.useState<string>('')
  const [totalWeight, setTotalWeight] = React.useState<string>('')

  const calculateTotal = React.useCallback(() => {
    const from = parseFloat(distanceFrom) || 0
    const in_ = parseFloat(distanceIn) || 0
    return from + in_
  }, [distanceFrom, distanceIn])

  React.useEffect(() => {
    const specs = {
      distanceFromObstruction: parseFloat(distanceFrom) || 0,
      heightOfObstruction: parseFloat(height) || 0,
      distanceIn: parseFloat(distanceIn) || 0,
      totalDistance: calculateTotal(),
      totalWeight: parseFloat(totalWeight) || 0
    }
    onChange(specs)
  }, [distanceFrom, height, distanceIn, calculateTotal, totalWeight, onChange])

  return (
    <Card className="p-4 space-y-4">
      <h3 className="font-medium text-lg">Job Specifications</h3>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        <div className="space-y-2">
          <Label htmlFor="distanceFrom">Distance From Obstruction (ft)</Label>
          <Input
            id="distanceFrom"
            type="number"
            placeholder="Enter distance..."
            value={distanceFrom}
            onChange={(e) => setDistanceFrom(e.target.value)}
          />
        </div>
        <div className="space-y-2">
          <Label htmlFor="height">Height of Obstruction (ft)</Label>
          <Input
            id="height"
            type="number"
            placeholder="Enter height..."
            value={height}
            onChange={(e) => setHeight(e.target.value)}
          />
        </div>
        <div className="space-y-2">
          <Label htmlFor="distanceIn">Distance In (ft)</Label>
          <Input
            id="distanceIn"
            type="number"
            placeholder="Enter distance..."
            value={distanceIn}
            onChange={(e) => setDistanceIn(e.target.value)}
          />
        </div>
        <div className="space-y-2">
          <Label>Total Distance (ft)</Label>
          <div className="h-10 px-3 py-2 rounded-md border border-gray-300 bg-gray-50">
            {calculateTotal()}
          </div>
        </div>
        <div className="space-y-2">
          <Label htmlFor="totalWeight">Total Weight (tons)</Label>
          <Input
            id="totalWeight"
            type="number"
            placeholder="Enter total weight..."
            value={totalWeight}
            onChange={(e) => setTotalWeight(e.target.value)}
          />
        </div>
      </div>
    </Card>
  )
}
