"use client"

import * as React from "react"
import { Check, ChevronsUpDown } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
} from "@/components/ui/command"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"

interface PhoneSelectProps {
  value: string
  onChange: (value: string) => void
}

export function PhoneSelect({ value, onChange }: PhoneSelectProps) {
  const [open, setOpen] = React.useState(false)
  const [phones, setPhones] = React.useState<string[]>(() => {
    // Load saved phone numbers from localStorage (only in browser)
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('savedPhones')
      return saved ? JSON.parse(saved) : []
    }
    return []
  })

  const addNewPhone = (phone: string) => {
    if (!phones.includes(phone)) {
      const newPhones = [...phones, phone]
      setPhones(newPhones)
      // Save to localStorage
      if (typeof window !== 'undefined') {
        localStorage.setItem('savedPhones', JSON.stringify(newPhones))
      }
    }
  }

  const removePhone = (phone: string) => {
    const newPhones = phones.filter(p => p !== phone)
    setPhones(newPhones)
    if (typeof window !== 'undefined') {
      localStorage.setItem('savedPhones', JSON.stringify(newPhones))
    }
  }

  const handleSelect = (currentValue: string) => {
    onChange(currentValue)
    setOpen(false)
    if (currentValue.trim()) {
      addNewPhone(currentValue.trim())
    }
  }

  const formatPhoneNumber = (input: string) => {
    // Remove all non-numeric characters
    const cleaned = input.replace(/\D/g, '')
    
    // Format the number as it's being typed
    if (cleaned.length === 0) return ''
    if (cleaned.length <= 3) return `(${cleaned}`
    if (cleaned.length <= 6) return `(${cleaned.slice(0,3)}) ${cleaned.slice(3)}`
    if (cleaned.length <= 10) return `(${cleaned.slice(0,3)}) ${cleaned.slice(3,6)}-${cleaned.slice(6)}`
    return `(${cleaned.slice(0,3)}) ${cleaned.slice(3,6)}-${cleaned.slice(6,10)}`
  }

  const handleInputChange = (search: string) => {
    const formattedNumber = formatPhoneNumber(search)
    onChange(formattedNumber)
  }

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          role="combobox"
          aria-expanded={open}
          className="w-full justify-between bg-white hover:bg-gray-50"
        >
          {value
            ? formatPhoneNumber(value)
            : "Enter phone number..."}
          <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-full p-0" align="start">
        <Command className="rounded-lg border shadow-md">
          <CommandInput 
            placeholder="Search or enter new phone..."
            onValueChange={handleInputChange}
            value={formatPhoneNumber(value)}
          />
          <CommandEmpty className="py-3 px-4 text-sm text-gray-500">
            No phone found. Type to add new.
          </CommandEmpty>
          {phones.length > 0 && (
            <CommandGroup heading="Saved Phone Numbers" className="px-1">
              {phones.map((phone) => (
                <CommandItem
                  key={phone}
                  value={phone}
                  onSelect={() => handleSelect(phone)}
                  className="flex justify-between items-center"
                >
                  <div className="flex items-center">
                    <Check
                      className={cn(
                        "mr-2 h-4 w-4",
                        value === phone ? "opacity-100" : "opacity-0"
                      )}
                    />
                    {formatPhoneNumber(phone)}
                  </div>
                  <button
                    className="text-red-500 hover:text-red-700"
                    onClick={(e) => {
                      e.stopPropagation()
                      removePhone(phone)
                    }}
                    aria-label={`Remove ${phone}`}
                  >
                    &times;
                  </button>
                </CommandItem>
              ))}
            </CommandGroup>
          )}
        </Command>
      </PopoverContent>
    </Popover>
  )
}
