"use client"

import * as React from "react"
import { Check, ChevronsUpDown } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
} from "@/components/ui/command"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"

interface Contact {
  name: string;
  phone: string;
}

interface ContactSelectProps {
  value: string;
  onChange: (value: string) => void;
  onPhoneSelect: (phone: string) => void;
}

export function ContactSelect({ value, onChange, onPhoneSelect }: ContactSelectProps) {
  const [open, setOpen] = React.useState(false)
  const [contacts, setContacts] = React.useState<Contact[]>(() => {
    // Load saved contacts from localStorage
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('savedContacts')
      return saved ? JSON.parse(saved) : []
    }
    return []
  })

  const addNewContact = (name: string, phone: string = '') => {
    if (!contacts.find(c => c.name === name)) {
      const newContacts = [...contacts, { name, phone }]
      setContacts(newContacts)
      if (typeof window !== 'undefined') {
        localStorage.setItem('savedContacts', JSON.stringify(newContacts))
      }
    }
  }

  const updateContactPhone = (name: string, phone: string) => {
    const newContacts = contacts.map(c => 
      c.name === name ? { ...c, phone } : c
    )
    setContacts(newContacts)
    if (typeof window !== 'undefined') {
      localStorage.setItem('savedContacts', JSON.stringify(newContacts))
    }
  }

  const removeContact = (name: string) => {
    const newContacts = contacts.filter(c => c.name !== name)
    setContacts(newContacts)
    if (typeof window !== 'undefined') {
      localStorage.setItem('savedContacts', JSON.stringify(newContacts))
    }
  }

  const handleSelect = (currentValue: string) => {
    onChange(currentValue)
    setOpen(false)
    const selectedContact = contacts.find(c => c.name === currentValue)
    if (selectedContact) {
      onPhoneSelect(selectedContact.phone)
    }
  }

  const handleInputChange = (search: string) => {
    onChange(search)
  }

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          role="combobox"
          aria-expanded={open}
          className="w-full justify-between bg-white hover:bg-gray-50"
        >
          {value
            ? value
            : "Select or enter contact name..."}
          <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-full p-0" align="start">
        <Command className="rounded-lg border shadow-md">
          <CommandInput 
            placeholder="Search or enter new contact..."
            onValueChange={handleInputChange}
            value={value}
          />
          <CommandEmpty className="py-3 px-4 text-sm text-gray-500">
            No contact found. Type to add new.
          </CommandEmpty>
          {contacts.length > 0 && (
            <CommandGroup heading="Saved Contacts" className="px-1">
              {contacts.map((contact) => (
                <CommandItem
                  key={contact.name}
                  value={contact.name}
                  onSelect={() => handleSelect(contact.name)}
                  className="flex justify-between items-center"
                >
                  <div className="flex items-center">
                    <Check
                      className={cn(
                        "mr-2 h-4 w-4",
                        value === contact.name ? "opacity-100" : "opacity-0"
                      )}
                    />
                    <div>
                      <div>{contact.name}</div>
                      {contact.phone && (
                        <div className="text-sm text-gray-500">{contact.phone}</div>
                      )}
                    </div>
                  </div>
                  <button
                    className="text-red-500 hover:text-red-700"
                    onClick={(e) => {
                      e.stopPropagation()
                      removeContact(contact.name)
                    }}
                    aria-label={`Remove ${contact.name}`}
                  >
                    &times;
                  </button>
                </CommandItem>
              ))}
            </CommandGroup>
          )}
        </Command>
      </PopoverContent>
    </Popover>
  )
}
