/// <reference types="@types/google.maps" />

"use client"

import React, { useEffect, useRef, useState } from "react"

interface PickupLocationMapProps {
  apiKey: string
  location: { lat: number; lng: number } | null
  onLocationChange: (location: { lat: number; lng: number }) => void
}

export default function PickupLocationMap({ apiKey, location, onLocationChange }: PickupLocationMapProps) {
  const mapRef = useRef<HTMLDivElement>(null)
  const [map, setMap] = useState<google.maps.Map | null>(null)
  const [marker, setMarker] = useState<google.maps.Marker | null>(null)

  useEffect(() => {
    if (!mapRef.current || !apiKey) return

    const initMap = () => {
      const initialPosition = location || { lat: 41.8781, lng: -87.6298 } // Default to Chicago, IL
      const mapInstance = new google.maps.Map(mapRef.current!, {
        center: initialPosition,
        zoom: 12,
        mapTypeControl: false,
        fullscreenControl: false,
        streetViewControl: false
      })

      const markerInstance = new google.maps.Marker({
        map: mapInstance,
        position: initialPosition,
        draggable: true,
        title: 'Drag to set location'
      })

      markerInstance.addListener("dragend", () => {
        const pos = markerInstance.getPosition()
        if (pos) {
          onLocationChange({ lat: pos.lat(), lng: pos.lng() })
        }
      })

      setMap(mapInstance)
      setMarker(markerInstance)
    }

    // If Google Maps is already loaded, initialize map
    if (window.google?.maps) {
      initMap()
      return
    }

    // Otherwise, wait for the callback from LocationPicker
    window.initGoogleMapsCallback = () => {
      initMap()
    }
  }, [apiKey, location, onLocationChange])

  useEffect(() => {
    if (marker && location) {
      marker.setPosition(location)
      map?.panTo(location)
    }
  }, [location, marker, map])

  return <div ref={mapRef} className="w-full h-64 rounded-md border" />
}
