"use client"

import React from "react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import PickupLocationMap from "./pickup-location-map"

declare global {
  interface Window {
    google: typeof google
    initGoogleMapsCallback?: () => void
  }
}

interface LocationPickerProps {
  value: string
  onChange: (value: string) => void
  apiKey: string
}

interface LocationPickerState {
  location: { lat: number; lng: number } | null
  address: string
  predictions: google.maps.places.AutocompletePrediction[]
  isOpen: boolean
  isLoading: boolean
  sessionToken?: google.maps.places.AutocompleteSessionToken
}

export class LocationPicker extends React.Component<LocationPickerProps, LocationPickerState> {
  private autocompleteService: google.maps.places.AutocompleteService | null = null
  private placesService: google.maps.places.PlacesService | null = null
  private searchTimeout: NodeJS.Timeout | null = null
  private isInitialized: boolean = false
  private scriptElement: HTMLScriptElement | null = null

  constructor(props: LocationPickerProps) {
    super(props)
    this.state = {
      location: null,
      address: props.value || '',
      predictions: [],
      isOpen: false,
      isLoading: false,
      sessionToken: undefined
    }
  }

  componentDidMount() {
    this.loadGoogleMapsScript()
  }

  componentDidUpdate(prevProps: LocationPickerProps) {
    if (prevProps.value !== this.props.value && !this.state.isOpen) {
      this.setState({ address: this.props.value || '' })
    }
  }

  componentWillUnmount() {
    if (this.searchTimeout) {
      clearTimeout(this.searchTimeout)
    }
    if (typeof window !== 'undefined') {
      delete window.initGoogleMapsCallback
    }
  }

  private loadGoogleMapsScript = () => {
    if (typeof window === 'undefined') return;

    // If Google Maps is already loaded
    if (window.google?.maps?.places) {
      this.initializeServices()
      return
    }

    // If script is already being loaded
    const existingScript = document.querySelector('script[src*="maps.googleapis.com"]')
    if (existingScript) {
      if (!this.isInitialized) {
        window.initGoogleMapsCallback = () => {
          this.initializeServices()
        }
      }
      return
    }

    // Load the script
    const script = document.createElement('script')
    script.src = `https://maps.googleapis.com/maps/api/js?key=${this.props.apiKey}&libraries=places&callback=initGoogleMapsCallback&loading=async`
    script.async = true
    script.defer = true
    
    window.initGoogleMapsCallback = () => {
      this.initializeServices()
    }

    document.head.appendChild(script)
    this.scriptElement = script
  }

  private initializeServices = () => {
    if (!window.google?.maps?.places) return false;
    if (this.isInitialized) return true;

    try {
      // Initialize AutocompleteService first
      this.autocompleteService = new window.google.maps.places.AutocompleteService()

      // Create a hidden map element for PlacesService
      const mapDiv = document.createElement('div')
      mapDiv.style.visibility = 'hidden'
      mapDiv.style.position = 'absolute'
      mapDiv.style.left = '-9999px'
      document.body.appendChild(mapDiv)

      const map = new window.google.maps.Map(mapDiv, {
        center: { lat: 41.8781, lng: -87.6298 }, // Chicago
        zoom: 12,
        disableDefaultUI: true
      })

      // Initialize PlacesService
      this.placesService = new window.google.maps.places.PlacesService(map)

      // Initialize session token
      this.setState({
        sessionToken: new window.google.maps.places.AutocompleteSessionToken()
      }, () => {
        // Clean up the temporary map div after initialization
        document.body.removeChild(mapDiv)
      })

      this.isInitialized = true
      return true
    } catch (error) {
      console.error('Error initializing Google Places services:', error)
      this.isInitialized = false
      return false
    }
  }

  private handleAddressChange = async (input: string) => {
    this.setState({ 
      address: input,
      isLoading: input.length >= 3
    })
    this.props.onChange(input)
    
    if (this.searchTimeout) {
      clearTimeout(this.searchTimeout)
    }

    if (input.length < 3) {
      this.setState({
        predictions: [],
        isOpen: false,
        isLoading: false
      })
      return
    }

    this.searchTimeout = setTimeout(async () => {
      if (!this.autocompleteService) {
        const servicesInitialized = this.initializeServices()
        if (!servicesInitialized) {
          console.warn('Google Places service not initialized yet')
          this.setState({ isLoading: false })
          return
        }
      }

      try {
        const { sessionToken } = this.state
        const request: google.maps.places.AutocompletionRequest = {
          input,
          types: ['address'],
          componentRestrictions: { country: 'us' }
        }

        if (sessionToken) {
          request.sessionToken = sessionToken
        }

        const response = await this.autocompleteService!.getPlacePredictions(request)
        
        this.setState({
          predictions: response.predictions,
          isOpen: true,
          isLoading: false
        })
      } catch (error) {
        console.error('Error getting predictions:', error)
        this.setState({
          predictions: [],
          isOpen: false,
          isLoading: false
        })
      }
    }, 300)
  }

  private handlePredictionSelect = async (placeId: string) => {
    if (!this.placesService) return

    try {
      const { sessionToken } = this.state
      const request: google.maps.places.PlaceDetailsRequest = {
        placeId,
        fields: ['formatted_address', 'geometry'],
        sessionToken
      }

      this.placesService.getDetails(request, (result, status) => {
        if (status === google.maps.places.PlacesServiceStatus.OK && result?.formatted_address && result.geometry?.location) {
          const newLocation = {
            lat: result.geometry.location.lat(),
            lng: result.geometry.location.lng()
          }
          
          this.setState({
            location: newLocation,
            address: result.formatted_address,
            isOpen: false,
            sessionToken: undefined,
            predictions: []
          })

          this.props.onChange(result.formatted_address)

          // Create a new session token for the next search
          this.setState({
            sessionToken: new window.google.maps.places.AutocompleteSessionToken()
          })
        }
      })
    } catch (error) {
      console.error('Error getting place details:', error)
      this.setState({ isOpen: false, predictions: [] })
    }
  }

  private handleLocationChange = async (newLocation: { lat: number; lng: number }) => {
    this.setState({ location: newLocation })
    try {
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/geocode/json?latlng=${newLocation.lat},${newLocation.lng}&key=${this.props.apiKey}`
      )
      const data = await response.json()
      if (data.results && data.results[0]) {
        const newAddress = data.results[0].formatted_address
        this.setState({ address: newAddress })
        this.props.onChange(newAddress)
      }
    } catch (error) {
      console.error('Error getting address:', error)
    }
  }

  private handleAddressSubmit = async () => {
    const { address } = this.state
    if (!address) return

    try {
      const response = await fetch(
        `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURIComponent(address)}&key=${this.props.apiKey}`
      )
      const data = await response.json()
      if (data.results && data.results[0] && data.results[0].geometry) {
        const { lat, lng } = data.results[0].geometry.location
        this.setState({ location: { lat, lng } })
      }
    } catch (error) {
      console.error('Error getting coordinates:', error)
    }
  }

  render() {
    const { address, predictions, isOpen, isLoading, location } = this.state

    return (
      <div className="space-y-4">
        <div className="space-y-2">
          <div className="relative">
            <div className="flex gap-2">
              <div className="relative flex-1">
                <Input
                  id="location-input"
                  type="text"
                  placeholder="Start typing to search address..."
                  value={address}
                  onChange={(e) => this.handleAddressChange(e.target.value)}
                  className="w-full pr-8"
                  autoComplete="off"
                  name="location"
                  aria-label="Search for an address"
                  aria-expanded={isOpen}
                  aria-controls={isOpen ? "location-suggestions" : undefined}
                  aria-describedby="location-hint"
                />
                <div id="location-hint" className="sr-only">
                  Type to search for an address. Use arrow keys to navigate suggestions.
                </div>
                {isLoading && (
                  <div className="absolute right-3 top-1/2 -translate-y-1/2">
                    <div className="animate-spin h-4 w-4 border-2 border-gray-500 rounded-full border-t-transparent" />
                  </div>
                )}
              </div>
              <Button 
                type="button" 
                variant="outline"
                onClick={this.handleAddressSubmit}
              >
                Search
              </Button>
            </div>

            {isOpen && (address.length >= 3 || predictions.length > 0) && (
              <div 
                id="location-suggestions"
                role="listbox"
                className="absolute z-50 mt-1 w-full bg-white rounded-md border border-gray-200 shadow-lg max-h-[200px] overflow-y-auto"
              >
                <div className="py-1">
                  {isLoading && predictions.length === 0 && (
                    <div className="px-4 py-2 text-sm text-gray-500 flex items-center gap-2">
                      <div className="animate-spin h-4 w-4 border-2 border-gray-500 rounded-full border-t-transparent" />
                      <span>Searching addresses...</span>
                    </div>
                  )}
                  {!isLoading && predictions.length === 0 && (
                    <div className="px-4 py-2 text-sm text-gray-500">
                      No matching addresses found
                    </div>
                  )}
                  {predictions.map((prediction) => (
                    <div
                      key={prediction.place_id}
                      role="option"
                      aria-selected={false}
                      onClick={() => this.handlePredictionSelect(prediction.place_id)}
                      className="px-4 py-2 text-sm hover:bg-gray-100 cursor-pointer flex items-center gap-2"
                    >
                      {prediction.description}
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
        <PickupLocationMap
          apiKey={this.props.apiKey}
          location={location}
          onLocationChange={this.handleLocationChange}
        />
      </div>
    )
  }
}
