"use client"

import * as React from "react"
import { Check, ChevronsUpDown } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Command,
  CommandEmpty,
  CommandGroup,
  CommandInput,
  CommandItem,
} from "@/components/ui/command"
import {
  Popover,
  PopoverContent,
  PopoverTrigger,
} from "@/components/ui/popover"

interface CustomerSelectProps {
  value: string;
  onChange: (value: string) => void;
}

export function CustomerSelect({ value, onChange }: CustomerSelectProps) {
  const [open, setOpen] = React.useState(false)
  const [customers, setCustomers] = React.useState<string[]>(() => {
    // Load saved customers from localStorage
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('savedCustomers')
      return saved ? JSON.parse(saved) : []
    }
    return []
  })

  const addNewCustomer = (name: string) => {
    if (!customers.includes(name)) {
      const newCustomers = [...customers, name]
      setCustomers(newCustomers)
      if (typeof window !== 'undefined') {
        localStorage.setItem('savedCustomers', JSON.stringify(newCustomers))
      }
    }
  }

  const removeCustomer = (name: string) => {
    const newCustomers = customers.filter(c => c !== name)
    setCustomers(newCustomers)
    if (typeof window !== 'undefined') {
      localStorage.setItem('savedCustomers', JSON.stringify(newCustomers))
    }
  }

  const handleSelect = (currentValue: string) => {
    onChange(currentValue)
    setOpen(false)
  }

  const handleInputChange = (search: string) => {
    onChange(search)
  }

  return (
    <Popover open={open} onOpenChange={setOpen}>
      <PopoverTrigger asChild>
        <Button
          variant="outline"
          role="combobox"
          aria-expanded={open}
          className="w-full justify-between bg-white hover:bg-gray-50"
        >
          {value
            ? value
            : "Select or enter customer name..."}
          <ChevronsUpDown className="ml-2 h-4 w-4 shrink-0 opacity-50" />
        </Button>
      </PopoverTrigger>
      <PopoverContent className="w-full p-0" align="start">
        <Command className="rounded-lg border shadow-md">
          <CommandInput 
            placeholder="Search or enter new customer..."
            onValueChange={handleInputChange}
            value={value}
          />
          <CommandEmpty className="py-3 px-4 text-sm text-gray-500">
            No customer found. Type to add new.
          </CommandEmpty>
          {customers.length > 0 && (
            <CommandGroup heading="Saved Customers" className="px-1">
              {customers.map((customer) => (
                <CommandItem
                  key={customer}
                  value={customer}
                  onSelect={() => handleSelect(customer)}
                  className="flex justify-between items-center"
                >
                  <div className="flex items-center">
                    <Check
                      className={cn(
                        "mr-2 h-4 w-4",
                        value === customer ? "opacity-100" : "opacity-0"
                      )}
                    />
                    {customer}
                  </div>
                  <button
                    className="text-red-500 hover:text-red-700"
                    onClick={(e) => {
                      e.stopPropagation()
                      removeCustomer(customer)
                    }}
                    aria-label={`Remove ${customer}`}
                  >
                    &times;
                  </button>
                </CommandItem>
              ))}
            </CommandGroup>
          )}
        </Command>
      </PopoverContent>
    </Popover>
  )
}
