"use client"

import { useState } from 'react'
import { Card } from "@/components/ui/card"
import { LocationPicker } from "@/components/location-picker"
import { Calendar } from "@/components/ui/calendar"
import { CustomerSelect } from "@/components/customer-select"
import { ContactSelect } from "@/components/contact-select"
import { PhoneSelect } from "@/components/phone-select"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { CarSelect } from "@/components/car-select"
import { TimeSelect } from "@/components/time-select"
import { JobSpecsCalculator } from "@/components/job-specs-calculator"

const carOptions = [
  { label: 'Telehandler', value: 'telehandler' },
  { label: 'Spyder Crane', value: 'spyder crane' },
  { label: 'Carry Deck', value: 'carry deck' },
  { label: 'Boom Truck', value: 'boom truck' },
  { label: '35 Ton', value: '35 ton' },
  { label: '40 Ton', value: '40 ton' },
  { label: '50 Ton', value: '50 ton' },
  { label: '70 Ton', value: '70 ton' },
  { label: '90 Ton', value: '90 ton' },
  { label: '120 Ton', value: '120 ton' },
  { label: '155 Ton', value: '155 ton' },
  { label: '200 Ton', value: '200 ton' },
  { label: '240 Ton', value: '240 ton' },
  { label: '275 Ton', value: '275 ton' },
  { label: '300 Ton', value: '300 ton' },
  { label: '360 Ton', value: '360 ton' },
  { label: '450 Ton', value: '450 ton' },
  { label: '550 Ton', value: '550 ton' },
]

export default function CarOrderPage() {
  const [orderType, setOrderType] = useState<'save' | 'schedule'>('schedule')
  const [customerName, setCustomerName] = useState('')
  const [contactName, setContactName] = useState('')
  const [phoneNumber, setPhoneNumber] = useState('')
  const [successMessage, setSuccessMessage] = useState('')
  
  const handlePhoneSelect = (phone: string) => {
    setPhoneNumber(phone)
  }

  const clearForm = () => {
    setCustomerName('')
    setContactName('')
    setPhoneNumber('')
    setSelectedCar('')
    setCustomCar('')
    setSelectedDates([])
    setSelectedTime('')
    setLocation('')
    setAdditionalInfo('')
    setJobSpecs({
      distanceFromObstruction: 0,
      heightOfObstruction: 0,
      distanceIn: 0,
      totalDistance: 0
    })
  }
  const [selectedCar, setSelectedCar] = useState('')
  const [customCar, setCustomCar] = useState('')
  const [selectedDates, setSelectedDates] = useState<Date[]>([])
  const [selectedTime, setSelectedTime] = useState('')
  const [location, setLocation] = useState('')
  const [orderTimestamp, setOrderTimestamp] = useState<Date>()
  const [error, setError] = useState('')
  const [additionalInfo, setAdditionalInfo] = useState('')
  const [textareaHeight, setTextareaHeight] = useState('100px')

  // Auto-resize textarea
  const handleTextareaInput = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setAdditionalInfo(e.target.value)
    e.target.style.height = 'auto'
    e.target.style.height = `${e.target.scrollHeight}px`
    setTextareaHeight(`${e.target.scrollHeight}px`)
  }
  const [jobSpecs, setJobSpecs] = useState({
    distanceFromObstruction: 0,
    heightOfObstruction: 0,
    distanceIn: 0,
    totalDistance: 0
  })

  const handleSubmit = (e: React.FormEvent, action: 'save' | 'schedule' = 'schedule') => {
    e.preventDefault()
    setError('')
    setSuccessMessage('')

    // Basic validation for save
    if (!customerName) {
      setError('Please enter customer name')
      return
    }

    // Additional validation for schedule
    if (action === 'schedule') {
      if (!contactName) {
        setError('Please enter contact name')
        return
      }
      if (!phoneNumber) {
        setError('Please enter contact phone')
        return
      }
      if (!selectedCar) {
        setError('Please select a car model')
        return
      }
      if (selectedCar === 'custom' && !customCar) {
        setError('Please enter a custom car model')
        return
      }
      if (selectedDates.length === 0) {
        setError('Please select at least one pickup date')
        return
      }
      if (!selectedTime) {
        setError('Please select a pickup time')
        return
      }
      if (!location) {
        setError('Please enter a pickup location')
        return
      }
    }

    // Create timestamp
    const timestamp = new Date()
    setOrderTimestamp(timestamp)
    
    // Save customer name to localStorage
    if (typeof window !== 'undefined') {
      const savedCustomers = localStorage.getItem('savedCustomers')
      const customers = savedCustomers ? JSON.parse(savedCustomers) : []
      if (!customers.includes(customerName)) {
        customers.push(customerName)
        localStorage.setItem('savedCustomers', JSON.stringify(customers))
      }

      // Save contact info to localStorage only when scheduling
      if (action === 'schedule' && contactName && phoneNumber) {
        const savedContacts = localStorage.getItem('savedContacts')
        const contacts = savedContacts ? JSON.parse(savedContacts) : []
        const existingContact = contacts.find((c: any) => c.name === contactName)
        if (!existingContact) {
          contacts.push({ name: contactName, phone: phoneNumber })
          localStorage.setItem('savedContacts', JSON.stringify(contacts))
        } else if (existingContact.phone !== phoneNumber) {
          // Update phone number if changed
          const updatedContacts = contacts.map((c: any) => 
            c.name === contactName ? { ...c, phone: phoneNumber } : c
          )
          localStorage.setItem('savedContacts', JSON.stringify(updatedContacts))
        }
      }
    }

    // Prepare order details
    const orderDetails = {
      type: action,
      customerName,
      ...(action === 'schedule' && {
        contactName,
        phoneNumber,
      car: selectedCar === 'custom' ? customCar : selectedCar,
      scheduledDates: selectedDates,
      scheduledTime: selectedTime,
      location,
      jobSpecifications: jobSpecs,
      additionalInfo: additionalInfo
    }),
      orderCreated: timestamp
    }
    
    console.log('Order submitted:', orderDetails)
    
    // Show success message and optionally clear form
    setSuccessMessage(action === 'save' ? 'Order saved successfully!' : 'Order scheduled successfully!')
    if (action === 'schedule') {
      clearForm()
    }
  }

  return (
    <div className="container mx-auto p-4 min-h-screen bg-gradient-to-b from-gray-50 to-gray-100">
      <Card className="max-w-2xl mx-auto p-6 space-y-8 shadow-lg">
        <div className="space-y-4">
          <div className="space-y-2">
            <h1 className="text-3xl font-bold text-center">Job Scheduler</h1>
            <p className="text-gray-500 text-center">Schedule your crane service and onsite details</p>
          </div>

          <div className="flex justify-center gap-4">
            <Button
              type="button"
              variant={orderType === 'save' ? 'default' : 'outline'}
              onClick={() => setOrderType('save')}
              className="w-32"
            >
              Save Order
            </Button>
            <Button
              type="button"
              variant={orderType === 'schedule' ? 'default' : 'outline'}
              onClick={() => setOrderType('schedule')}
              className="w-32"
            >
              Schedule Order
            </Button>
          </div>

          <p className="text-sm text-gray-500 text-center">
            {orderType === 'save' 
              ? 'Save mode: Only customer name is required'
              : 'Schedule mode: All fields are required except additional pickup instructions'
            }
          </p>

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          {successMessage && (
            <Alert variant="default" className="bg-green-50 text-green-800 border-green-200">
              <AlertDescription>{successMessage}</AlertDescription>
            </Alert>
          )}
        </div>

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Car Selection */}
          <div className="space-y-4">
            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Customer Name
                </label>
                <CustomerSelect 
                  value={customerName}
                  onChange={setCustomerName}
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Contact Name
                </label>
                <ContactSelect
                  value={contactName}
                  onChange={setContactName}
                  onPhoneSelect={handlePhoneSelect}
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700">
                  Contact Phone
                </label>
                <PhoneSelect
                  value={phoneNumber}
                  onChange={setPhoneNumber}
                />
              </div>
            </div>
          </div>

          <label className="block text-sm font-medium text-gray-700">
            Select Crane Size
          </label>
          <CarSelect value={selectedCar} onChange={setSelectedCar} options={carOptions} />

          {selectedCar === 'custom' && (
            <Input
              type="text"
              placeholder="Enter custom car model"
              value={customCar}
              onChange={(e) => setCustomCar(e.target.value)}
              className="mt-2"
            />
          )}

          {/* Job Specifications Calculator */}
          <div className="mt-6">
            <JobSpecsCalculator onChange={setJobSpecs} />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Date Selection */}
            <div className="space-y-4">
            <div className="space-y-1">
              <label className="block text-sm font-medium text-gray-700">
                Select Onsite Dates
              </label>
              <p className="text-sm text-gray-500">Select one or more dates for your crane service</p>
            </div>
              <div className="space-y-3">
                <div className="bg-white rounded-lg shadow-sm border p-3">
                  <Calendar
                    mode="multiple"
                    selected={selectedDates}
                    onSelect={(dates) => {
                      if (!dates) {
                        setSelectedDates([])
                        return
                      }
                      setSelectedDates(Array.isArray(dates) ? dates : [dates])
                    }}
                    className="rounded-md"
                    disabled={(date) => date < new Date()}
                    fromDate={new Date()}
                  />
                </div>
                <div className="flex items-center justify-between">
                  <p className="text-sm text-gray-600">
                    {selectedDates.length > 0 
                      ? `${selectedDates.length} date${selectedDates.length > 1 ? 's' : ''} selected`
                      : 'Click dates to select multiple days'}
                  </p>
                  {selectedDates.length > 0 && (
                    <button
                      type="button"
                      onClick={() => setSelectedDates([])}
                      className="text-sm text-gray-500 hover:text-gray-700 hover:underline"
                    >
                      Clear selection
                    </button>
                  )}
                </div>
              </div>
            </div>

            <div className="space-y-4">
              {/* Time Selection */}
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700">
                  Select Onsite Time
                </label>
                <TimeSelect 
                  value={selectedTime}
                  onChange={setSelectedTime}
                  includeLeaveTimeOption={true}
                />
              <p className="text-sm text-gray-500">Select onsite arrival time or leave time</p>
              </div>

              {/* Location */}
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700">
                  Jobsite Location
                </label>
                <LocationPicker
                  value={location}
                  onChange={setLocation}
                  apiKey="AIzaSyBA4nuagnB-WQcZzyJfEujVK_yrNimd8SU"
                />
                <p className="text-sm text-gray-500">Enter the address or drop a pin on the map</p>
              </div>
            </div>
          </div>

          {/* Additional Information */}
          <div className="space-y-2 w-full">
            <label className="block text-sm font-medium text-gray-700">
              Additional Information
            </label>
            <div className="relative w-full">
              <textarea
                placeholder="Enter any additional details about your crane requirements..."
                value={additionalInfo}
                onChange={handleTextareaInput}
                style={{ height: textareaHeight }}
                className="w-full min-h-[120px] rounded-md border border-gray-300 p-4 resize-none overflow-hidden transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-black focus:border-transparent text-base leading-relaxed"
                rows={1}
              />
            </div>
            <p className="text-sm text-gray-500">
              Add any specific requirements, access details, or special instructions
            </p>
          </div>

          {/* Submit Buttons */}
          <div className="flex gap-4">
            <Button
              type="button"
              onClick={(e) => handleSubmit(e, 'save')}
              className="flex-1 bg-gray-100 text-gray-900 hover:bg-gray-200 transition-colors"
              disabled={!customerName}
            >
              Save Order
            </Button>
            <Button
              type="submit"
              className="flex-1 bg-black text-white hover:bg-gray-800 transition-colors"
              disabled={orderType === 'schedule' && (!selectedCar || selectedDates.length === 0 || !selectedTime || !location)}
            >
              Schedule Order
            </Button>
          </div>
        </form>

        {/* Schedule Creation Timestamp */}
        {orderTimestamp && (
          <div className="mt-4 p-4 bg-gray-100 rounded-md">
            <p className="text-sm text-gray-600">
              Schedule created on: {orderTimestamp.toLocaleString()}
            </p>
          </div>
        )}
      </Card>
    </div>
  )
}
